        </el-row>
      </el-form>
    </div> -->
    <el-button style="margin-left: 8px;" @click="dialogFormVisible = true" type="success">添加/修改问题分类</el-button>
    <el-button style="margin-left: 8px;" @click="$router.push('/article/questioncreate')" type="success"
      >新建问题</el-button
    >
    <el-table :data="tableData" class="list" v-loading="loading">
      <el-table-column prop="id" label="编号" align="center" width="80" />
      <el-table-column prop="problem" label="标题" align="center" width="240" />
      <el-table-column prop="content" label="内容" align="center" width="90" />
      <el-table-column prop="category" label="文章类型" align="center" />
      <el-table-column prop="sort" label="排序" align="center" />
      <el-table-column prop="uptime_date" label="更新时间" align="center" />
      <el-table-column prop="addtime_date" label="添加时间" align="center" />
      <el-table-column prop="address" align="center" label="操作" width="150">
        <template slot-scope="scope">
          <el-button @click="editItem(scope.row)" type="primary" size="small">编辑</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
    <el-dialog title="问题类目详情" :visible.sync="dialogFormVisible" width="70%">
      <el-table :data="tagList">
        <el-table-column prop="id" label="编号" align="center" />
        <el-table-column prop="title" align="center" label="分类">
          <template slot-scope="scope">
            <el-input type="text" v-model="scope.row.title" placeholder="请输入标签类型"></el-input>
          </template>
        </el-table-column>
        <el-table-column prop="sort" align="center" label="排序">
          <template slot-scope="scope">
            <el-input type="number" v-model="scope.row.sort" placeholder="请输入问题排序"></el-input>
          </template>
        </el-table-column>
        <el-table-column prop="address" align="center" label="操作">
          <template slot-scope="scope">
            <el-button @click="updateRow(scope.row)" type="primary" size="small">确认</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="addTag">增加问题类目</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'

export default {
  data() {
    return {
      dialogFormVisible: false,
      loading: false,
      tableData: [],
      count: 0,
      page_curren: 1,
      addtime: [],
      tagList: [],
    }
  },
  mounted() {
    this.getTag()
    this.FetchList()
  },
  methods: {
    updateRow(row) {
      console.log(row)
      Fetch('/article/problem/' + (row.id ? 'update' : 'create'), {
        ...row,
      }).then(() => {
        this.$message({
          message: '标签' + (row.id ? '修改' : '创建') + '成功',
          type: 'success',
        })
        this.getTag()
        this.FetchList()
      })
    },
    addTag() {
      this.tagList.push({
        id: '',
        title: '',
        sort: '',
      })
    },
    getTag() {
      Fetch('/article/problem/search')
        .then(r => {
          this.tagList = r.data.list
        })
        .catch(res => {
          Message.error(res.msg)
        })
    },
    FetchList() {
      this.loading = true
      Fetch('/article/problemanswer/search')
        .then(r => {
          this.tableData = r.data.list
          this.count = r.data.count
          this.page_curren = Math.floor(r.data.page_curren)
        })
        .catch(res => {
          Message.error(res.msg)
        })
      this.loading = false
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    onSubmit() {
      this.page_curren = 1
      this.FetchList()
    },
    editItem(row) {
      this.$router.push('/article/questionedit/' + row.id)
    },
    removeItem(row) {
      this.$confirm('是否确认删除此条数据?', undefined, {
        type: 'warning',
        beforeClose: (action, instance, done) => {
          if (action === 'confirm') {
            instance.confirmButtonLoading = true
            Fetch('/article/art/remove', { id: row.id })
              .then(() => {
                instance.confirmButtonLoading = false
                Message.success('删除成功')
                done()
                this.FetchList()
              })
              .catch(() => {
                instance.confirmButtonLoading = false
                Message.error('删除失败')
              })
          } else {
            done()
          }
        },
      })
    },
  },
}
</script>
